@import "./src/global.scss";

.slideDot {
    display: inline-block;
    top:0px;
    cursor: pointer;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    margin: 15px 7px 0px;
    background-color: #c4c4c4;

   
    
    &.active {
        background-color: #f25997;
    }
}
